@import url("https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,400;0,700;1,400;1,700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Raleway";
  margin: 0 auto;
  max-width: 1440px;
  width: 90vw;
  height: 100px;
}
body div {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

nav {
  width: 90%;
  height: 100px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
nav img {
  width: 138px;
}
nav ul {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 50%;
}
nav ul li {
  list-style: none;
}
nav ul li .link {
  font-size: 16px;
  text-decoration: none;
  color: black;
  margin: 0 5px;
}
nav ul li .link.active {
  color: #028a0f;
}
nav ul li .link:hover {
  color: #028a0f;
}
nav button {
  font-size: 16px;
  color: #ffffff;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border: none;
  padding: 0 25px;
  height: 43px;
  background: linear-gradient(135deg, #028a0f 0%, #90d296 100%);
}
nav button:hover {
  background: linear-gradient(135deg, #90d296 0%, #028a0f 100%);
}

.footer {
